@import 'assets/styles/fonts.scss';

.main {
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    width: 100%;
    margin: 24px 0 12px 0;
    padding: 12px 0;
    background-color: #eeeeee;

    .formLabel{
        text-align: center;
    }

    .PaperBlock{
        padding: 8px 4px 0px 4px;
    }

    .switch{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 16px;
    }
    & .sliderContainer{
        padding: 32px 12px 0px 12px;
    }
}
