@import "assets/styles/fonts.scss";

.main {
    width: 100%;
}

.title404 {
    color: #dcdcdc;
    font-weight: 500;
    font-size: 404px;
    text-align: center;
}
.titleBlock {
    text-align: center;
}

.h1 {
    @extend .fonts-headline;

    margin-bottom: 20px;

    color: #555555;
    font-weight: 500;
}

.link {
    font-weight: 500;
    font-size: 20px;
    color: #7aacff;
}

@media screen and (max-width: $MAX_WIDTH_CONTAINER) {
    .title404 {
        font-size: 300px;
    }
}
@media screen and (max-width: $MAX_TABLET_SCREEN) {
    .title404 {
        font-size: 250px;
    }
}
@media screen and (max-width: $MAX_MOBILE_SCREEN) {
    .title404 {
        font-size: 150px;
    }
}
