@import 'assets/styles/index.scss';

.main {
    @extend .materialBlock;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 420px;
    height: 250px;
    padding: 12px;
    box-sizing: border-box;

    border: 1px solid #333;
    border-radius: 15px;
    background-color: #eee;
}

.panel{
    @extend .materialBlock;

    position: absolute;
    right: 10px;
    bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 150px;
    padding: 4px;
    border: 1px solid #333;
    border-radius: 12px;
}

.panel_top{
    position: relative;
}
.panel_bottom{
    display: flex;
    justify-content: flex-end;
}

.key{
    position: absolute;
    top: 8px;
    right: 12px;
}
.mic{
    position: absolute;
    top: 12px;
    left: 12px;
    width: 40px;
    height: 40px;
}
.logo{
    position: absolute;
    top: 18px;
    left: 80px;
    width: 80px;
}
.title{
    position: absolute;
    top: 70px;
    left: 4px;
    text-align: center;
    font-size: 13px;

    &_text{
        margin-bottom: 4px;
    }
}

.controls{
    margin-right: 24px;
}


